import Store from '@/store'
import JavascriptAPI from '@/utils/JavascriptAPI'
import Api from '@/service/service'

export const additionalKeysToMessage = (keys, msg) => {
  let newMsg = `
${msg}

--- Message was sent from our groove widget at ${keys.fromPage} ---
--- ADDITIONAL INFO ---
`
  for (const key in keys) {
    const splitted = key.split(/(?=[A-Z])/).join(' ')
    const fixedKey = splitted[0].toUpperCase() + splitted.slice(1)
    if (keys[key] !== '') {
      newMsg += `
${fixedKey}: ${keys[key]}
`
    }
  }
  return newMsg
}

export const getAdditionlKeys = (urlParams, data) => {
  const obj = {}
  for (const key in data) {
    if (!Object.prototype.hasOwnProperty.call(urlParams, key)) {
      obj[key] = data[key]
    }
  }
  return obj
}

export const sendMessageHelper = async (formData, extraUserData) => {
  const res = await Api.createTicket(formData, extraUserData)
  return res
}

export const objectToUrlQuerys = (obj) => {
  let url = ''
  const encode = (val) => encodeURIComponent(val)
  for (const i in obj) {
    url += `&${encode(i)}=${encode(obj[i])}`
  }
  return url
}

const getConfig = async () => {
  let obj = {}
  const pollLimit = 150
  let poll = 0
  await new Promise((resolve, reject) => {
    if (top === self) {
      resolve(null)
      return null
    }
    const checkConfigs = () => {
      if (window._PaymentIQ_GROOVE_API_CONFIGS_) {
        obj = window._PaymentIQ_GROOVE_API_CONFIGS_
        resolve(window._PaymentIQ_GROOVE_API_CONFIGS_)
      }
    }
    JavascriptAPI._GET_CONFIGS_()
    setInterval(() => {
      if (poll < pollLimit) {
        checkConfigs()
        poll++
      } else {
        resolve(null)
      }
    }, 50)
  })
  Store.dispatch('setConfig', obj)
  const formData = {
    ...obj.additionalTicketData && { ...obj.additionalTicketData },
    ...obj.page_url && { page_url: obj.page_url },
    ...obj.fromPage && { fromPage: obj.fromPage }
  }
  if (Object.keys(formData).length > 0) {
    Store.dispatch('setFormData', formData)
  }
  if (obj.textKeys && typeof obj.textKeys === 'object') {
    Store.dispatch('setTextKeys', obj.textKeys)
  }
  return obj
}

export const setUpThemeProps = async () => {
  const themeParams = await getConfig()
  const { theme } = themeParams
  const { background, color } = theme || {}
  const root = document.querySelector('html').style
  const themeProps = {
    '--theme-bg': background || '#3d6363',
    '--theme-color': color || '#ffffff'
  }
  for (const theme in themeProps) {
    root.setProperty(theme, themeProps[theme])
  }
  return null
}
