<template>
  <v-container>
    <v-row class="floating-btn">
      <v-col
        cols="12"
        sm="12"
        md="6"
      >
        <v-btn
          v-show="!showContactForm"
          @click="showContantFormMethod"
          class="theme-bg theme-color"
          dark
          absolute
          top
          right
          rounded
          x-large
        >
          <div class="transition-floating-btn" >
            <v-icon>mdi-message</v-icon>
            <span class="pl-3 subheading">{{ needHelpText }}</span>
          </div>
        </v-btn>

        <v-btn
          v-show="showContactForm"
          @click="showContantFormMethod"
          class="theme-bg theme-color"
          dark
          absolute
          top
          right
          rounded
          x-large
        >
          <div class="transition-floating-btn">
            <v-icon large>mdi-close</v-icon>
          </div>
        </v-btn>

      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapActions, mapState } from 'vuex'
import JavascriptAPI from '@/utils/JavascriptAPI'

export default {
  name: 'Floating-help-btn',
  data: () => ({
    hidden: false
  }),
  computed: {
    ...mapState({
      showContactForm: ({ appState }) => appState.showContactForm,
      config: ({ appState }) => appState.config,
      textKeys: ({ appState }) => appState.textKeys
    }),
    needHelpText () {
      return this.textKeys.needHelpText || '{needHelpText}'
    }
  },
  methods: {
    ...mapActions([
      'setShowContactForm'
    ]),
    showContantFormMethod () {
      const { showContactForm } = this
      const payload = {}
      if (!showContactForm) {
        payload.height = this.config.height || '800px'
        payload.width = this.config.width || '600px'
      } else {
        payload.height = '80px'
        payload.width = '256px'
      }
      JavascriptAPI._SET_APP_HEIGHT_WIDTH_(payload)
      this.setShowContactForm(!showContactForm)
    }
  }
}
</script>

<style lang="scss" scoped>
.floating-btn {
  position: fixed;
  bottom: 60px;
  right: 30px;
}
