function hasQueryParams (route) {
  return !!Object.keys(route.query).length
}

export default (to, from, next) => {
  try {
    if (!hasQueryParams(to) && hasQueryParams(from)) {
      next({ name: to.name, query: from.query })
    } else {
      next()
    }
  } catch (err) {
    console.error(err)
  }
}
