import { APP_STATE } from '../mutation-types'

const showContactForm = () => window.location.search.includes('showContactForm')

const state = {
  showContactForm: showContactForm(), // should be false at prod
  config: {},
  textKeys: {},
  sendingMsg: false,
  ticketStatus: ''
}

const mutations = {
  [APP_STATE.SET_SHOW_CONTACT_FORM] (state, payload) {
    state.showContactForm = payload
  },
  [APP_STATE.SET_CONFIG] (state, payload) {
    state.config = payload
  },
  [APP_STATE.SET_SENDING_MSG] (state, payload) {
    state.sendingMsg = payload
  },
  [APP_STATE.SET_TICKET_STATUS] (state, payload) {
    state.ticketStatus = payload
  },
  [APP_STATE.SET_TEXT_KEYS] (state, payload) {
    state.textKeys = payload
  }
}

export default {
  state,
  mutations
}
