import eventTypes from './eventTypes'
import Store from '@/store'

const sendPostMessage = (data, frame = null) => {
  if (frame) {
    frame.postMessage(data, '*')
  } else {
    parent.postMessage(data, '*')
  }
}

const __API__ = () => {
  window.addEventListener('message', (event) => {
    const eventData = event.data
    const { eventType, payload } = eventData
    switch (eventType) {
      case eventTypes.RECEIVE_PIQ_GROOVE_CONFIGS:
        return APICallback.setConfigs(payload)
      case eventTypes.SET_ADDITIONAL_TICKET_DATA:
        return APICallback.setAdditionalData(payload)
    }
  })
}

const APICallback = {
  setConfigs (payload) {
    window._PaymentIQ_GROOVE_API_CONFIGS_ = payload
  },
  setAdditionalData (payload) {
    Store.dispatch('setExtraUserData', payload)
    _SET_APP_HEIGHT_WIDTH_({
      height: Store.state.appState.config.height || '800px',
      width: Store.state.appState.config.width || '600px'
    })
    Store.dispatch('setShowContactForm', true)
  }
}

const _GET_CONFIGS_ = () => {
  sendPostMessage(
    {
      eventType: eventTypes.GET_PIQ_GROOVE_CONFIGS
    })
}

const _SET_APP_HEIGHT_WIDTH_ = (payload) => {
  sendPostMessage(
    {
      eventType: eventTypes.SET_APP_HEIGHT_AND_WIDTH,
      payload
    })
}

export default {
  __API__,
  _GET_CONFIGS_,
  _SET_APP_HEIGHT_WIDTH_
}
