import Vue from 'vue'
import VueRouter from 'vue-router'
import authGuard from './auth-guard'
const Confirm = () => import(/* webpackChunkName: "Confirm" */ '@/pages/Confirm.vue')
const ContactPage = () => import(/* webpackChunkName: "ContactPage" */ '@/pages/ContactPage.vue')
const Status = () => import(/* webpackChunkName: "Status" */ '@/pages/Status.vue')

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'ContactPage',
    component: ContactPage
  },
  {
    path: '/status',
    name: 'Status page',
    component: Status
  },
  {
    path: '/confirm',
    name: 'Confirm page',
    component: Confirm
  }
]

const AppRouter = new VueRouter({
  base: '/piq-groove-widget/',
  mode: 'history',
  routes
})

AppRouter.beforeEach(authGuard)

export default AppRouter
