import { APP_STATE } from '../mutation-types'

const state = {
  formData: {
    subject: '',
    userName: '',
    email: '',
    category: '',
    message: ''
  },
  extraUserData: {}
}

const mutations = {
  [APP_STATE.SET_FORM_DATA] (state, payload) {
    state.formData = {
      ...state.formData,
      ...payload
    }
  },
  [APP_STATE.SET_EXTRA_USER_DATA] (state, payload) {
    state.extraUserData = payload
  }
}

export default {
  state,
  mutations
}
