import { objectToUrlQuerys, getAdditionlKeys, additionalKeysToMessage } from '@/utils/helpers'
import FetchApi from './index'
import { resCallbackName } from '@/utils/types'

// We are just reusing the support ticket widget endpoint/api not the documented v1 API.

const baseUrl = 'https://widget-backend.groovehq.com/api/tickets.json?consent=true'

const createTicket = async (data, extraUserData) => {
  const { email, message, subject, page_url: pageUrl } = data
  const urlParams = {
    subject,
    message,
    tab_id: 0,
    page_url: pageUrl,
    page_title: null,
    referrer: null,
    'customer[email]': email,
    widget_uuid: '2566f48f-5974-fb31-8d80-f128d834ea54',
    callback: resCallbackName
  }
  const additionalKeys = {
    ...getAdditionlKeys(urlParams, data),
    ...extraUserData
  }
  if (Object.keys(additionalKeys).length > 0) {
    const newMessage = additionalKeysToMessage(additionalKeys, message)
    urlParams.message = newMessage
  }
  process.env.NODE_ENV !== 'production' && console.log(urlParams.message)
  const apiEndpoint = await FetchApi.GET(`${baseUrl}${objectToUrlQuerys(urlParams)}`)
  return apiEndpoint
}

export default {
  createTicket
}
