const fetchApi = async (url, options) => {
  const resStatusMock = window._PaymentIQ_GROOVE_API_CONFIGS_ && window._PaymentIQ_GROOVE_API_CONFIGS_.resStatusMock
  if (resStatusMock) {
    // Mocked status request can be set via config int. API. Set status 200 for success, anything else is failed.
    return {
      status: resStatusMock,
      mockedResponse: true
    }
  } else {
    return await fetch(url, options)
  }
}

const proxyurl = 'https://cors-anywhere.herokuapp.com/' // Adds cross origin allow to response headers

export default {
  async GET (url) {
    return await fetchApi(`${proxyurl}${url}`, { method: 'GET' })
  }
}
